#info{
    width:100%; 
    height:130vh;
    padding: 110px 0px;
    //border: 2px solid red;
    
    .info__inner{
        width:1180px; 
        margin:0 auto; 
        .info__top__txt{
            height: 50px;
            border-bottom: 1px solid var(--color-gray-01);
            margin-bottom: 30px;
            .info__top__txt__left{    
                h1{
                font:20px 'open sans';
                color:var(--color-darkgray-01);
                font-weight: 300;
                float:left;
                margin-right: 850px;
                padding-bottom: 20px;
                letter-spacing: 1px;
                }
            }
            .info__top__txt__right{    
                button{
                width: 100px; height: 30px;
                font:14px/26px 'open sans';
                color:var(--color-darkgray-01);
                font-weight: 300;
                float: left;
                margin-right: 10px;
                padding-bottom: 0px;
                }
                :hover{
                    color: var(--color-gray-03);
                    
               }
            }
        } 

        .info__wrap{
                width:100%; 
                
    
                article{
                width:25%; 
                float: left;
                padding:0px;
                margin-bottom: 50px;

                    .info__project__txt{
                        padding:20px;
                        h2{
                            font: 16px 'open sans';
                            letter-spacing: 1px;
                            color: var
                            (--color-darkgray-01);
                            padding-bottom: 20px;
                            
    
                        }
                        p{
                            font: 14px 'open sans';
                            color: var(--color-darkgray-00);
    
                        }
                    }

                    .pic{
                        width:100%; 
                        height:50vh; 
                        overflow:hidden;
                        
                        img{
                            width:100%; 
                            height:100%; 
                            object-fit:cover;
                            transition:0.7s; 
                            filter:grayscale(.7)
                            //display: none;
                        }

                        &:hover img{
                            filter: none;
                            transform: scale(1.1);
                            transition: all 0.8s;
                        }
                    }
                }
        }
    }
}